@mixin flex-center()
    display: flex
    align-items: center
    justify-content: center

*
    margin: 0
    padding: 0
    box-sizing: border-box
    text-decoration: none
    list-style: none
    border: none
    outline: none
    font-family: 'Roboto', sans-serif
:root
    --chat-input-height: 150px
body
    background-color: #1d1d1d

.chat-container
    position: relative
    width: 100%
    margin: 0 auto
    height: 100vh
    display: flex
    overflow: hidden
    .chat
        width: 100%
        margin-bottom: 0.5rem 
        .pre-image-component
            .image-upload-label
                display: flex
                width: 100%
                height: 10rem
            input[type="file"]
                display: none
            
    
.chat-input-container
    width: 100%
    display: flex
    bottom: 0
    left: 0
    position: absolute
    padding: 1rem

    .chat-input-area
        margin: 0 auto
        width: 100%
        max-width: 48rem
        position: relative
        display: flex
        flex-direction: column
        background-color: #292929
        border-width: 1px
        border-color: transparent
        border-radius: 26px
        padding: 0.5rem
        transition: all 0.2s
        &:focus-within
            // background-color: #333333
            border-color: #e5e7eb
            box-shadow: 0 0 #0000

        .image-preview-container
            display: flex
            gap: 0.5rem
            padding: 0.5rem
            overflow-x: auto
            .image-preview-item
                position: relative
                flex: 0 0 auto
                .close-icon
                    position: absolute
                    top: 0
                    right: 0
                    color: #ff4d4d
                    cursor: pointer
                    font-size: 1.25rem
                    &:hover
                        color: #ff1a1a
                img
                    width: 70px
                    height: 70px
                    border-radius: 0.5rem
                    object-fit: cover

        .input-textarea
            width: 100%
            background-color: transparent
            color: #dedede
            font-size: 1rem
            line-height: 1.5rem 
            padding: 0.75rem 1rem
            outline: none
            resize: none
            overflow: hidden
            min-height: 56px
            max-height: 200px
        
        .icon-container
            display: flex
            justify-content: space-between
            align-items: center
            padding: 0 0.5rem 0.25rem 0.5rem

            .media-buttons
                display: flex
                gap: 0.5rem
                .media-button
                    padding: 0.5rem
                    background: transparent
                    border-radius: 9999px
                    color: #c0c0c0
                    cursor: pointer
                    @include flex-center()
                    &:hover
                        background-color: #353535
                        transition: background-color 0.2s
            
            .send-button
                display: flex
                gap: 0.5rem
                align-items: center
                button
                    padding: 0.5rem
                    border-radius: 9999px
                    color: #e4e4e4
                    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05)
                    background: #191919
                    cursor: pointer
                    @include flex-center()
                    
                    &:hover
                        background-color: #656565
                        transition: background-color 0.2s
                    &:disabled
                        cursor: not-allowed
                        background-color: #292929

.image-uploader
    width: 100%
    .pre-image-component
        width: 100%
        margin: 0 auto
        max-width: 48rem
        height: calc( 100vh - var(--chat-input-height)  )
        .pre-image-container
            position: relative
            border-style: dashed
            border-width: 2px 
            border-radius: 0.75rem
            padding: 2rem
            text-align: center
            width: 100%
            height: 100%
            cursor: pointer
            transition: all 0.2s
            display: flex
            flex-direction: column
            align-items: center
            justify-content: center
            gap: 0.75rem
            border-color: #d1d5db
            
            &:hover
                border-color: #d1d5db
                background-color: #f9fafb
            &.dragging
                border-color: #3b82f6
                background-color: #eff6ff
                transform: scale(1.02)

            p
                color: #9ca3af
                font-size: 0.75rem
                margin-top: 0.25rem

            .upload-icon
                padding: 1rem
                border-radius: 9999px
                background-color: #f3f4f6
                color: #6b7280
                &.dragging
                    background-color: #bfdbfe
                    color: #2563eb

            .upload-text
                font-size: 0.875rem
                color: #4b5563
                span
                    font-weight: 600
                    color: #3b82f6

.dialog-container
    display: flex
    flex-direction: column
    align-items: center
    overflow-y: auto
    height: calc( 100vh -  var(--dialog-container-height))
        
    &::-webkit-scrollbar
        width: 6px
    &::-webkit-scrollbar-track 
        // background: transparent
        background: #565656
        border-radius: 3px
    &::-webkit-scrollbar-thumb 
            background: #b1b1b1
            
            border-radius: 3px
        

    .message-container
        display: flex
        margin: 0.5rem auto
        width: 100%        
        max-width: 48rem

        .message-box
            min-height: 1rem
            padding: 1rem 0.85rem
            color: #ccc
            font-weight: 400
            font-size: .92rem
            &.user
                max-width: 28rem   
                margin-left: auto 
                border-radius: 1rem
                background-color: #242424
                .message-images
                    display: flex
                    flex-wrap: wrap
                    gap: 0.4rem
                    width: 100%
                    margin-bottom: 0.8rem
                    img
                        border-radius: 0.5rem
                        width: 8.4rem
                        height: 12rem
                        object-fit: cover
